@import url('https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap');

body {
    width: 100vw;
    overflow-x: hidden;
    scroll-behavior: smooth;
    font-family: 'Playfair Display', serif;
}
.col1 {width: 8.33vw}
.col2 {width: 16.66vw}
.col3 {width: 25vw}
.col4 {width: 33.33vw}
.col5 {width: 41.66vw}
.col6 {width: 50vw}
.col7 {width: 58.33vw}
.col8 {width: 66.33vw}
.col9 {width: 75vw}
.col10 {width: 83.33vw}
.col11 {width: 91.33vw}
.col12 {width: 100vw}

.jumboBG {
    background-color: #000;
    height: 100vh;
    opacity: 0.6;
    filter: blur(6px);
    overflow: hidden;
    box-sizing: border-box;
    position: absolute;
    top: -3px;
    left: -3px;
    display: flex;
    flex-direction: column;
    /* flex-wrap: wrap; */
}
.geometry {
  z-index: -1;
}
.geometryTop,
.geometryBottom {
  display: flex;
  flex-direction: row;
  flex-wrap: no-wrap;
  overflow-x: hidden;
  justify-content: center;
}
.welcomeText {
    z-index: 2;
    background: none;
    position: absolute;
    top: 0;
    left: 0;
    vertical-align: center;
    text-align: center;
    color: #0f0f0f;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
}
.welcomeText h1 {
    box-sizing: border-box;
    line-height: 9vw;
    margin: 12vw 16.6vw;
    font-size: 5vw;
    cursor: default;
}
.hidden {
    visibility: hidden;
}
/* ================================================================================================== */

.navbar {
    box-sizing: border-box;
    height: 55px;
    background: #0f0f0f;
    position: absolute;
    top: 92.7vh;
    left: 0;
    opacity: 0.99;
    z-index: 99;
    display: flex;
    flex-direction: row;
    color: #8d8b8b;
}
.isSticky {
    position: fixed;
    top: 0;
    left: 0;
}
.navbarmenuitem {
    height: 100%;
    list-style: none;
    box-sizing: border-box;
    text-align: center;
    padding-top: 1vw;
    font-weight: bold;
    font-size: 1.3vw;
}

.navbarmenuitem1:hover,
.navbarmenuitem4:hover,
.navbarmenuitem5:hover,
.navbarmenuitem6:hover,
.navbarmenuitem7:hover,
.navbarmenuitem9:hover,
.navbarmenuitem10:hover {
    cursor: pointer;
    background-color: #8d8b8b;
    color: #0f0f0f;
}
.navbarmenuitem9,
.navbarmenuitem10 {
  background-size: 3vw;
  background-repeat: no-repeat;
  background-position: center;
}
.navbarmenuitem9 {
  background-image: url(./images/githubLogoLight.png);
}
.navbarmenuitem10 {
  background-size: contain;
  background-image: url(./images/linkedinLogoLight.png);
}
.navbarmenuitem9:hover {
  background-image: url(./images/githubLogoDark.png);
}
.navbarmenuitem10:hover {
  background-image: url(./images/linkedinLogoDark.png);
}
