.modal {
  width: 33.33vw;
  height: 54vw;
  background-color: #8d8b8b;
  border: 3px solid #0f0f0f;
  border-radius: 1vw;
  margin: 0 auto;
  transform: scale(0.75);
  position: absolute;
  top: 139vw;
  left: 33.33vw;
  z-index: 99;
  box-sizing: border-box;
  text-align: center;
  padding: 15px;
}
.modal button {
  height: 30px;
  width: 30px;
  cursor: pointer;
  position: relative;
  left: 50%;
  top: -12px;
  border-radius: 50%;
  box-sizing: border-box;
  text-align: center;
  border: 1px solid black;
}
.modal button:hover {
  opacity: 0.7;
}
.modal h2 {
  margin-top: 20px;
  margin-bottom: -10px;
}
.modal p {
  box-sizing: border-box;
  padding-left: 20px;
  padding-right: 20px;
  line-height: 22px;
  font-size: 20px;
}
.modal p hr {
  width: 70%;
  margin-top: 15px;
  margin-bottom: 15px;
  background-color: #0f0f0f;
  border-color: #0f0f0f;
}
.modal p a,
.modal a {
  color: black;
  text-decoration: none;
  font-weight: bold;
}
.modal p a:hover,
.modal p a:link,
.modal p a:active,
.modal p a:visited,
.modal a:hover,
.modal a:link,
.modal a:active,
.modal a:visited {
  color: black;
}

