#codewarsLink,
#codewarsLink:hover,
#codewarsLink:link,
#codewarsLink:active,
#codewarsLink:visited {
  color: #8d8b8b;
}
.page5SideText {
  box-sizing: border-box;
  margin-left: 3vw;
  text-align: right;
  font-size: 0.9vw;
  padding-top: 20%;
}
.contact {
  text-align: center;
  padding-top: 2vw;
}

/* =======================================FOOTER===================================================== */

footer {
  text-align: center;
  font-size: 0.8vw;
  align-self: flex-end;
  height: 2%;
  padding-bottom: 1vw;
}
.emailLink:link,
.emailLink:visited,
.emailLink:hover,
.emailLink:active,
.emailLink {
  text-decoration: none;
  color: #8d8b8b;
}
/* ======================================MEDIA QUERIES==================================================== */

@media screen and (max-width: 1534px) {
  .page3 {
    height: 40vw;
  }
  .page2 {
    height: 40vw;
    top: 87vw;
  }
  .page4 {
    top: 127vw;
    height: 40vw;
  }
  .page5 {
    top: 167vw;
    height: 40vw;
  }
  .page3 h2{
    transform: scale(0.8);
    margin-top: 30px;
  }
  .logoContainer {
    top: 50px;
  }
  .projectGrid {
    top: 6vw;
  }
  .modal {
    top: 120vw;
  }
  .page5 h2 {
    transform: scale(0.8);
  }
}

@media screen and (max-width: 1177px) {
  .navbarmenuitem {
    font-size: 1.1vw;
  }
}